html, body, #root {
  height: 100%;
}

.App {
  background-color: #343d52;
  text-align: center;
  height: 100%;
}

.Header{
  width: 100%;
  height: 180px;
  background-color: #0c3e85;
  overflow: scroll;
  position: relative;
}

.Name {
  margin: 10px;
  font-size: 40px;
  font: 400 100px/1.3 'Oleo Script', Helvetica, sans-serif;
  color: #2b2b2b;
  text-shadow: 4px 4px 0px rgba(0,0,0,0.1);
}

.Links {
  position: absolute;
  bottom: 0;
  width: 100%;
  margin: 20px;
}

.Link {
  display: inline-block;
  margin-left: 100px;
  margin-right: 100px;
}

.Link a {
  color: inherit;
  text-decoration: none;
  padding: 10px;
  padding-top: 7px;
  padding-bottom: 7px;
  border: solid gray 1px;
  border-radius: 20%;
  background-color: lightgray;
}

.Home, .About, .Projects {
  margin: 20px;
}